.slider {
	z-index: 1;

	.swiper {
		.swiper-slide {
			position: relative;
			text-align: center;

			/* Center slide text vertically */
			display: flex;
			-webkit-box-pack: center;
			-ms-flex-pack: center;
			-webkit-justify-content: center;
			justify-content: center;
			-webkit-box-align: center;
			-ms-flex-align: center;
			-webkit-align-items: center;
			align-items: center;

			img {
				display: block;
				max-width: 100%;
				height: auto;
				object-fit: cover;
			}

			.text {
				top: 210px;
				left: 10px;
				width: 400px;
				height: 200px;
				position: absolute;
				font-weight: 700;
				font-size: 28px;
				color: white;
			}
		}
	}

	&.is-admin {

	}
}